#form-container {
  @include center-child;
  flex-direction: column;
  min-height: 90vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
  z-index: 5;
  @media (max-width: 768px) {
    min-height: 50vh;
  }
  .waves {
    position:absolute;
    z-index: -1;
    width: 100%;
  }
}
h3 {
  margin-top: 50px;

}
form{
    display: grid;
    width: 40%;
    min-width: 300px;
    max-width: 600px;
    margin: 0 auto;
    margin-top: 50px;
    gap: 10px;
    padding: 5px;
    background-color: $color-2;
    border-radius: 50px;
    z-index: 10;
    .input-container {
      display: flex;
      flex-direction: column;
      label {
        font-size: 1.2rem;
        text-align: center;
        width: 80%;
        margin: auto;
        background-position: center;
        background-size: 100%;
        @media screen and (max-width: 768px) {
          font-size: 1.1rem;
        }
      }
      input {
        height: 40px;
        font-size: 1.2rem;
        border-radius: 15px;
        border: none;
        padding: 0 10px;
        text-align: center;
        @media screen and (max-width: 768px) {
          height: 4vh;
          width: 100%;
        }
      }
      textarea {
        resize: vertical;
        min-height: 250px;
        max-height: 700px;
        font-size: 1rem;
        border-radius: 15px;
        border: none;
        padding: 5px 10px;
        @media screen and (max-width: 768px) {
          height: 1vh;
          width: 100%;
          min-height: 150px;
        }
      }
      .btn-submit {
        cursor: pointer;
        border-radius: 5px;
        margin-bottom: 50px;
        transition: 1s;
        background-color: $color-2;
        width: 60%;
        margin: 10px auto;
        @media screen and (max-width: 768px) {
          height: 5vh;
          width: 80%;
        }
        &:hover {
          border-radius: 20px;
          transition: 1s;
          letter-spacing: 5px;
          background-color: $color-4;
        }
      }     
      &:nth-child(1) {
        label {
         background-image: url('../../../public/img/up-burger.webp');         
        }
      }
      &:nth-child(2) {
        label {
          background-image: url('../../../public/img/mid-burger.webp');         
         }
      }
      &:nth-child(3) {
        label {
          background-image: url('../../../public/img/bottom-burger.webp');         
         }
      } 
    }
    @media (max-width: 1000px) {
      width: 98%;
    }
    .back-name, .back-email, .back-textarea, .back-btn {
      background-position: center;
      background-size: 100%;
      width: 50%;
      height: 250px;
      left: 25%;
      position: absolute;
      z-index: -1;
      @media (max-width: 1000px) {
        display: none;
      }
      @media (min-width: 1921px) {
        width: 40%;
        height: 650px;
        left: 30%;
      }
    }
    .back-name {
      top: 12%;
      background-image: url('../../../public/img/splat/splat\ 3.webp');
      @media (min-width: 1921px) {
        top: 5%;
      }
    }
    .back-email {
      top: 20%;
      background-image: url('../../../public/img/splat/splat\ 2.webp');
      @media (min-width: 1921px) {
        top: 10%;
      }
    }
    .back-textarea {
      width: 52%;
      top: 40%;
      height: 210px;
      background-image: url('../../../public/img/splat/Text\ splat.webp');
      @media (min-width: 1921px) {
        width: 40%;
        left: 31.5%;
        top: 43%;
        height: 250px;
      }
    }
    .back-btn {
      top: 68%;
      height: 100px;
      background-image: url('../../../public/img/splat/splat\ 1.webp');
      @media (min-width: 1921px) {
        top: 63%;
      }
    }
  }
  .about {
    @include center-child;
    .submarine {
      background: rgb(161,112,7);
      background: linear-gradient(0deg, rgba(161,112,7,1) 0%, rgba(238,189,37,1) 100%);
      border-radius: 30px 40px 90px 80px;
      border: 1px solid $black;
      margin-top: 80px;
      height: 80px;
      width: 350px;position: relative;
    .periscope-container {
      position:absolute;
      top: -40px;
      left: 20%;
      height: 41px;
      width: 42px;
      background: $black;
      box-sizing: border-box;
      clip-path: polygon(0 0, 66% 0, 66% 100%, 33% 100%, 33% 25%, 0 25%);
      .periscope {
        clip-path: polygon(0 0, 66% 0, 66% 100%, 33% 100%, 33% 25%, 0 25%);
        background-color: rgba(238,189,37,1);
        height: 40px;
        width: 40px;
        top: 1px;
        left: 1px;
        position:absolute;
      }      
    }
    .window-container {
      height: 22px;
      width: 22px;
      border-radius: 50%;
      position: absolute;
      left: 10%;
      top: 40%;
      .window {
        height: 20px;
        width: 20px;
        background-color: aqua;
        border-radius: 50%;
        position: absolute;
        left: 1px;
        top: 1px;
        outline: $black 2px dotted;
        .window-reflect {
          position: absolute;
          top: 15%;
          left:15%;
          height: 8px;
          width: 8px;
          border-radius: 50%;
          background-color: rgb(192, 243, 243);
          filter: drop-shadow(1px 3px 3px rgba(192, 243, 243, 0.5));
        }
      }
    }
    p {
      font-family: "font-4";
      font-size: 1.3rem;
      position: absolute;
      top: 40%;
      left: 20%;
    }
    .propeller, .propeller-2 {
      clip-path: ellipse(10% 50% at 50% 50%);
      height: 30px;
      width: 35px;
      background-color: $black;
      position: absolute;
      right:-10%;
    }
    .propeller {
      top: -10px;
    }
    .propeller-2 {
      top: 15px;
    }
    .propeller-rod {
      height: 3px;
      width: 20px;
      background-color: $black;
      position: absolute;
      top: 20%;
      right: -5%;
    }
    .rivets {
      position: absolute;
      border-radius: 50%;
      border: 1px solid black;
    }
  }   
  .bubbles-container {
    z-index: -10;
  .bubbles, .bubbles2, .bubbles3 {
    position: absolute;
    border-radius: 50%;
    border: 1px solid black;
    .bubble-dot {
      position: absolute;
      top: 15%;
      left:15%;
      height: 3px;
      width: 3px;
      border-radius: 50%;
      border: 1px solid black;
    }
  }
  .bubbles {
    top: -10px;
    right: -20px;
    height: 15px;
    width: 15PX;
  }
  .bubbles2 {
    top: 10px;
    right: -30px;
    height: 12px;
    width: 12PX;
  }
  .bubbles3 {
    top: 0;
    right: -35px;
    height: 10px;
    width: 10PX;
  }    
  }   

}

  .message-shadow {
    filter: drop-shadow(-1px 6px 3px rgba(50, 50, 0, 0.5));
    position: absolute;
    top: 82%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    @media (min-width: 1921px) {
      top: 90%;
    }
  }
  .success, .error {
    width: 500px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
  }
  .success {
    background-image: url('../../../public/img/splat/message-success.webp');
    background-size: cover;
    background-position: center;
    font-size: 1.5rem;
    height: 80px;
    @media (min-width:1921px) {
      font-size: 1.6rem;
    }
  }
  .error {
    background-image: url('../../../public/img/splat/message-error.webp');
    background-size: cover;
    background-position: center;
    font-size: 1rem;
    height: 100px;
    @media (min-width:1921px) {
      font-size: 1.6rem;
      width: 750px;
      height: 200px;
    }
  }