.carousel-container {
    position: relative;
    width: 60%;
    margin: auto;
    flex-direction: column;
    min-height: 100vh;
    @include center-child;
    @media (max-width:1800px) {
        width: 80%;
    }
    h3 {
        margin: 50px auto;
    }
    .carousel-card {
        @include center-child;
        flex-direction: column;
        margin-top: -5%;
        gap: 10px;
        h2 {
            font-size: 3rem;
            margin-bottom: 20px;
        }
        img {
            height: 500px;
            width: auto;
            @media (max-width:2200px) {
                height: 350px;
            }
        }
        p {
            width: 80%;
        }
        a {
            padding: 10px;
            border-radius: 20px;
            border: 1px solid $black;
            background: $color-2;
            color: $black;
        }
    }
    .awssld {
        --content-background-color: #50a5de;
        --control-bullet-active-color: #546fe4;
        --control-bullet-color: rgba(152, 192, 212, 0.5);
        :nth-child(even) {
            --content-background-color: #546fe4;
            p {
                color: $white;
            }
        }
    }
    .awssld__wrapper {
        border-radius: 5px;
    }
    .awssld__controls__arrow-left:before, .awssld__controls__arrow-right:before, .awssld__controls__arrow-left:after, .awssld__controls__arrow-right:after {
        background-color: rgba(152, 192, 212, 0.9);
        width: 8px;
    }
    .awssld__bullets {
        bottom: -100px;
        :nth-child(even) {
            --control-bullet-active-color: #50a5de;
        }
    }
    .project-line, .project-line-2 {
        position: absolute;
        z-index: -1;
        opacity: .4;

    }
    .project-line {
        left: -20%;
        top: -20px;
    }
    .project-line-2 {
        bottom: -5%;
        right: -25%;
    }
}